      },
      inputfn(){
        if (!this.filterc) {
            //  这是空的时候触发的函数
          this.filterb = this.filtera
        }else{
    
  this.filterb = this.filtera.filter(item => {         

  const filterChars = this.filterc.toLowerCase().split('');

  for (let i = 0; i < filterChars.length; i++) {
    const char = filterChars[i];
    if (!item.name.toLowerCase().includes(char)) {
      return false; // 如果当前字符不在 item.name 中，立即返回 false
    }
  }
  return true; // 如果所有字符都在 item.name 中，返回 true
});
        }
      },
